@import (less) "./../../assets/styles/color.less";
:host /deep/ * {
	box-sizing: border-box;
}

.nbp-slider-storico-component {
	position: relative;
	padding: 0px;
	font-family: Arial Regular, Sans-Serif;
	font-size: 12px;
	float:left;
	width: 93%;
}


.centerButton {
	display: inline-block;
}

.icona {
	float:right;
	width: 5%;
	position: relative;
	&:before {
		font-family: icomoon !important;
		font-style: normal !important;
		font-weight: normal !important;
		font-variant: normal !important;
		text-transform: none !important;
		line-height: 0;
		-webkit-font-smoothing: antialiased;
		-moz-osx-font-smoothing: grayscale;
		//content: '';
		font-size: 35px;
		background-color: white;
		border-radius: 35px;
		margin-left: -8px;
		margin-top: 30px;
		cursor: pointer;
		color: #258900;
	}
}

.icona-pointer {
  cursor: pointer;
}

