// CSS x FILTRO
// ----------------------------------------
.success-container-open {
	width: 35px;
	height: 35px;
	cursor: pointer;
	// border-radius: 25px;
	position: absolute;
	margin-left: -40px;
	margin-top: -2px;
	// background-color: @green;
	// color: @white;
	background-color: @white;
	color: @green;
}

.hover:hover {
	.success-container-close {
		.success-icon.on-hover {
			display: inline;
		}
		.success-icon {
			display: none;
		}
	}
}

