.icon-valori-filtri-settati {
	width: 35px;
	height: 35px;
	cursor: pointer;
	border-radius: 25px;
	&:hover {
		background-color: @green;
		color: @white;
	}
	position: relative;
	background-color: @white;
	color: @green;
	border: 1px solid @green
}

.container-filtrisettati {
	width: 35px;
	height: 35px;
	cursor: pointer;
	border-radius: 25px;
	&:hover {
		background-color: @green;
		color: @white;
	}
	position: absolute;
	background-color: @white;
	color: @green;
	border: 1px solid @green
}

.arrow-filter {
	display: inline-block;
	position: relative;
	width: 0;
	height: 0;
	border-left: 12px solid transparent;
	border-right: 12px solid transparent;
	border-bottom: 12px solid @light_gray;
	// margin-right: 6.875em;
	margin-right: 122px;
}

.arrow-filter::before {
	content: '';
	position: absolute;
	top: 2px;
	width: 0;
	height: 0;
	border-left: 11px solid transparent;
	border-right: 11px solid transparent;
	border-bottom: 11px solid #f2f2f2;
	// left: -11px;
	left: -10.5px;
}

.success-icon {
	width: 35px;
	height: 35px;
	font-size: 35px;
	cursor: pointer;
	position: absolute;
	top: -15%;
	left: -1%;
