:host-context(.contrasto-on) {
	.success-container-open {
		background-color: @white;
		color: @black;
	}
	.success-container-close {
		&:hover {
			background-color: @white;
			color: @black;
		}
		background-color: @white;
		color: @black;
		// border: 1px solid @black;
	}
	.icon-valori-filtri-settati {
		&:hover {
			background-color: @black;
			color: @white;
		}
		background-color: @white;
		color: @black;
		border: 1px solid @black;
	}
	.container-filtrisettati {
		&:hover {
			background-color: @black;
			color: @white;
		}
		background-color: @white;
		color: @black;
		border: 1px solid @black;
	}
	.arrow-filter {
		border-bottom: 12px solid @black;
	}
	.arrow-filter::before {
		border-bottom: 11px solid @white;
	}
	.cancella-filtri {
		color: @black;
	}
	.cercaFiltra1 {
		color: @black;
	}
	.cercaFiltra2 {
		color: @black;
	}
	.ricercaEffettuata {
		color: @black;
	}
	// .hover:hover > .success-container-close {
	//     background-color: @black;
	//     color: @white;
	// }
	.hoverCancellaFiltri:hover > .icon-valori-filtri-settati {
		background-color: @black;
		color: @white;
	}
	.filter-panel {
		background-color: @white;
		;
		color: @black;
		border: 0.5px solid @black;
	}
	.gomma {
		color: @black;
		background-color: transparent;
	}
	.gomma_txt {
		color: @black;
	}
}
